import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { executeViewHooks } from '@angular/core/src/render3/instructions';

@Component({
  selector: 'app-task-dialog',
  templateUrl: './task-dialog.component.html',
  styleUrls: ['./task-dialog.component.css']
})
export class TaskDialogComponent implements OnInit {
  public active = false;
  operator: number;
  opnoin: string;
  completeContext: any;

  constructor() {
  }
  @Output() btnClickEvt = new EventEmitter<any>();
  ngOnInit() {
  }

  load(op: number) {
    this.operator = op;
    this.active = !this.active;
  }
  updateOpnion(opnoin: string): void {
    this.opnoin = opnoin;
  }
  /**
   * 执行审批
   */
  submit() {
    // 默认是审批通过
    let executeType = 'Circulate';
    if (this.operator === 1) {// 审批不通过
      if (!this.opnoin) {
        alert('审批意见不能为空！');
        return;
      } else {
        executeType = 'Terminate';
      }
    }
    if (this.operator === 2) {// 驳回到上一级
      if (!this.opnoin) {
        alert('审批意见不能为空！');
        return;
      } else {
        executeType = 'Reject';
      }
    }
    this.completeContext = {
      CirculationType: executeType,
      ApprovalOpinion: this.opnoin
    };
    this.btnClickEvt.emit(this.completeContext);
    this.active = false;
  }

  cancel() {
    this.active = false;
  }
}



