import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { ButtonsModule } from '@progress/kendo-angular-buttons';
import { GridModule } from '@progress/kendo-angular-grid';
import { TreeViewModule } from '@progress/kendo-angular-treeview';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { LabelModule } from '@progress/kendo-angular-label';
import { LayoutModule } from '@progress/kendo-angular-layout';
import { DropDownsModule } from '@progress/kendo-angular-dropdowns';
import { WindowModule } from '@progress/kendo-angular-dialog';

import { TaskCenterService } from './taskcenter.service';
import { TaskMainComponent } from './task-main/task-main.component';
import { TaskListComponent } from './task-list/task-list.component';
import { TaskNavComponent } from './task-nav/task-nav.component';
import { TaskDetailComponent } from './task-detail/task-detail.component';
import { TaskDialogComponent } from './task-dialog/task-dialog.component';
import { TaskFlowchartComponent } from './task-flowchart/task-flowchart.component';
import { FormQueryComponent } from './form-query/form-query.component';
import { TaskListToolbarComponent } from './task-list-toolbar/task-list-toolbar.component';
import { PluginContainerComponent } from './plugin-container/plugin-container.component';
import { TaskcenterRoots } from './taskcenter.routes';
import { EventBus } from './eventbus/event-bus';
import { TaskList } from './taskList';

@NgModule({
  imports: [
    CommonModule,
    // BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule,
    ButtonsModule,
    GridModule,
    InputsModule,
    LabelModule,
    LayoutModule,
    WindowModule,
    DropDownsModule,
    TreeViewModule,
    TaskcenterRoots
  ],
  declarations: [
    TaskMainComponent,
    TaskListComponent,
    TaskNavComponent,
    TaskDetailComponent,
    TaskDialogComponent,
    FormQueryComponent,
    TaskFlowchartComponent,
    TaskListToolbarComponent,
    PluginContainerComponent
  ],
  entryComponents: [],
  providers: [
    TaskCenterService,
    TaskList,
    EventBus
  ],
})
export class TaskcenterModule {}



