import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { of } from 'rxjs/observable/of';
import { TaskCenterService } from '../taskcenter.service';
import { TaskCategory } from '../TaskCategory';
import { TreeViewModel } from '../TreeViewModel';

@Component({
  selector: 'app-task-nav',
  templateUrl: './task-nav.component.html',
  styleUrls: ['./task-nav.component.css']
})
export class TaskNavComponent implements OnInit {
  @Output() selectedBizTypeIdChange = new EventEmitter<any>();
  public expandedKeys: any[] = ['0', '1', '2'];
  public selectedKeys: any[] = ['0_0'];
  public navSour: any[];
  // private data = of(NavDatas.result);
  public hasChildren = (item: any) => item.items && item.items.length > 0;
  public fetchChildren = (item: any) => of(item.items);
  constructor(protected taskService: TaskCenterService) {
  }
  ngOnInit() {
    this.taskService.fetchTaskCategories()
      .subscribe((result) => {
        result.value.map(e => this.taskService.toLowCase(e)); // OData改造暂时存在属性首字母不能转换的问题，兼容处理
        // 组织树格式数据
        const bindData: TreeViewModel<TaskCategory>[] = this.convertTreeData(result.value);
        this.navSour = bindData;
        this.selectedBizTypeIdChange.emit(bindData[0].items[0]);
      });
    // this.data.subscribe(val => {
    //   this.navSour = val;
    //   this.selectedBizTypeIdChange.emit(val[0].items[0]);
    // });
  }
  private toLowCase(jsonObj: Object) {
    if (typeof (jsonObj) === 'object') {
      for (const key of Object.keys(jsonObj)) {
        jsonObj[key.substring(0, 1).toLowerCase() + key.substring(1)] = jsonObj[key];
        delete (jsonObj[key]);
      }
    }
    return jsonObj;
  }
  /**
   * 导航树选中节点变更
   * @param param0 TreeItem
   */
  public handleSelection({ index, dataItem }: any): void {
    this.selectedKeys = [index];
    this.selectedBizTypeIdChange.emit(dataItem);
  }

  private convertTreeData(rows: TaskCategory[]): TreeViewModel<TaskCategory>[] {
    const treeVM: TreeViewModel<TaskCategory>[] = [];
    // get the top level nodes
    rows.forEach((item) => {
      if (!rows.find((r) => r.id !== item.id && r.id === item.parentId)) {
        treeVM.push({
          id: item.id,
          text: item.name,
          parentId: '',
          count: 0,
          items: [],
          value: item
        });
      }
    });
    treeVM.sort((x, y) => x.value.sortId > y.value.sortId ? 1 : -1);

    const toDo: TreeViewModel<TaskCategory>[] = [];
    for (let i = 0; i < treeVM.length; i++) {
      toDo.push(treeVM[i]);
    }
    while (toDo.length) {
      const node = toDo.shift();	// the parent node
      // get the children nodes
      for (let i = 0; i < rows.length; i++) {
        const item: TaskCategory = rows[i];
        if (item.id !== node.id && item.parentId === node.id) {
          const child = {
            id: item.id,
            text: item.name,
            parentId: node.id,
            count: 0,
            items: [],
            value: item
          };
          node.items.push(child);
          toDo.push(child);
        }
      }
      node.items.sort((x, y) => x.value.sortId > y.value.sortId ? 1 : -1);
    }
    return treeVM;
  }

}



