import { Component, OnInit, Compiler, Injector } from '@angular/core';
import { TaskCenterService } from '../taskcenter.service';

@Component({
  selector: 'app-taskmain',
  templateUrl: './task-main.component.html',
  styleUrls: ['./task-main.component.css']
})
export class TaskMainComponent implements OnInit {
  selectedBizTypeId: string;
  selectNodeName: string;
  showFlowList = false;
  constructor(
    compiler: Compiler,
    injector: Injector
  ) { }

  ngOnInit() {
  }
  onSelectedBizTypeIdChange(noteItem: any) {
    this.selectedBizTypeId = noteItem.id;
    if (!!noteItem.id) {
      this.selectNodeName = noteItem.text;
    }
    // this.showFlowList = this.selectedBizTypeId === 'workflowlist' ? true : false;
  }

}



