import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpService } from '@gsp-sys/rtf-common';
import { Observable } from 'rxjs/Observable';
import { forEach } from '@angular/router/src/utils/collection';
// import { ListData } from './assets/mockData';

@Injectable()
export class TaskCenterService {
  public TASK_CATTEGORY = '/api/runtime/wf/v1.0/taskcategories';
  public TASK_LIST = '/api/runtime/wf/v1.0/GSPTaskInfos';
  constructor(protected http: HttpService) {
    if (!environment.production) {
      const host = 'http://localhost:5000';
      this.TASK_LIST = host + this.TASK_LIST;
      this.TASK_CATTEGORY = host + this.TASK_CATTEGORY;
    }
  }
  /**
   * 查询任务分类
   */
  fetchTaskCategories(): Observable<any> {
    const requestUrl = this.TASK_CATTEGORY;
    return this.http.get(requestUrl);
  }
  /**
   * 查询任务列表
   * @param skip skip
   * @param top top
   * @param filter filter
   */
  fetchTaskList(skip: number, top: number, filter?: string): Observable<any> {
    const requestUrl = this.TASK_LIST + '?$skip=' + skip + '&$top=' + top + '&$orderby=StartTime&$count=true&$filter=' + filter;
    return this.http.get(requestUrl);
  }
  /**
   * 查询任务配置
   * @param categoryId categoryId
   */
  fetchTaskConfig(categoryId: string): Observable<any> {
    const requestUrl = `/api/runtime/wf/v1.0/taskconfigs/categoryId/${categoryId}`;
    return this.http.get(requestUrl);
  }

  excuteTask(taskId: string, context: any): Observable<any> {
    const url = `/api/runtime/wf/v1.0/Complete`;
    context.TaskId = taskId;
    const options = {
      completeContext: context
    };
    return this.http.post(url, options);
  }

  /**
   * 转换
   * @param jsonObj Object
   */
  toUpperCase(jsonObj: Object) {
    if (typeof (jsonObj) === 'object') {
      for (const key of Object.keys(jsonObj)) {
        jsonObj[key.substring(0, 1).toUpperCase() + key.substring(1)] = jsonObj[key];
        delete (jsonObj[key]);
      }
    }
    return jsonObj;
  }
  toLowCase(jsonObj: Object) {
    if (typeof (jsonObj) === 'object') {
      for (const key of Object.keys(jsonObj)) {
        jsonObj[key.substring(0, 1).toLowerCase() + key.substring(1)] = jsonObj[key];
        delete (jsonObj[key]);
      }
    }
    return jsonObj;
  }
}



