import { Routes, RouterModule } from '@angular/router';
import { TaskMainComponent } from './task-main/task-main.component';
import { TaskDetailComponent } from './task-detail/task-detail.component';
import { TaskFlowchartComponent } from './task-flowchart/task-flowchart.component';
import { FormQueryComponent } from './form-query/form-query.component';

const routes: Routes = [
    // { path: '', component: TaskMainComponent },
    { path: 'detail/:operator', component: TaskDetailComponent },
    { path: 'flowchart', component: TaskFlowchartComponent },
    { path: 'formquery', component: FormQueryComponent }
];

export const TaskcenterRoots = RouterModule.forChild(routes);



