import { Component, OnInit, Input, ViewChild, Compiler, Injector, ComponentRef } from '@angular/core';
import { GridDataResult, PageChangeEvent, SelectionEvent, SelectableSettings } from '@progress/kendo-angular-grid';
import { TaskCenterService } from '../taskcenter.service';
import { TaskDialogComponent } from '../task-dialog/task-dialog.component';
import { GspTask } from '../gspTask';
import { FrameworkService } from '@gsp-sys/rtf-common';
import { TaskColumnConfig } from '../TaskColumnConfig';
import { TaskList } from '../taskList';
import { PluginContainerComponent } from '../plugin-container/plugin-container.component';

@Component({
  selector: 'app-task-list',
  templateUrl: './task-list.component.html',
  styleUrls: ['./task-list.component.css']
})
export class TaskListComponent implements OnInit {
  @ViewChild(TaskDialogComponent)
  private approveDialog: TaskDialogComponent;

  @ViewChild(PluginContainerComponent)
  private pluginContainer: PluginContainerComponent;

  public toolbarContent: ComponentRef<any>;
  public gridView: GridDataResult;
  todoList: GspTask[] = [];
  // private pageIndex = 1;
  public pageSize = 20;
  public skip = 0;
  public selectedWorkitem;
  public enable = true;
  public selectableSettings: SelectableSettings = {
    enabled: true,
    mode: 'multiple'
  };
  private _bizTypeId: string;
  private _title: string;
  constructor(
    public taskService: TaskCenterService,
    protected frameworkSvc: FrameworkService,
    public taskList: TaskList
  ) { }

  @Input()
  set bizTypeId(name: string) {
    this._bizTypeId = (name && name.trim());
    if (!this._bizTypeId) {
      return;
    }
    this.taskService.fetchTaskConfig(this._bizTypeId).subscribe((result) => {
      if (result.fields) {
        this.columns = result.fields;
        this.getToDoList();
      } else {
        alert('没有显示列配置！');
      }
      // if (result.actions) {
      //   const toolbarPath = result.actions[0].path;
      //   this.taskList.lazyLoad(toolbarPath, null)
      //     .then((re) => alert(re));
      // } else {
      //   console.log(this._bizTypeId + '没有配置工具栏');
      // }
    });
    // 暂时写死了审批的工具扩展链接
    // this.taskList.lazyLoad('/platform/runtime/wf/web/wf-toolbar.module#
    // ./src/app / modules / wf - toolbar / wf - toolbar.module.js#WfToolbarModule', null)
    //  .then((re) => alert(re));
  }
  get bizTypeId(): string { return this._bizTypeId; }
  @Input()
  set pageTitle(title: string) {
    this._title = title;
  }
  get pageTitle(): string { return this._title; }
  columns: TaskColumnConfig[]; // 显示列配置
  ngOnInit() {
  }
  /**
   * 获取绑定列表数据
   */
  protected getToDoList(): void {
    let filter = '';
    if (this.bizTypeId) {
      filter = `CategoryId eq '${this.bizTypeId}' and State eq 2`;
    } else {
      return;
    }
    this.taskService.fetchTaskList(this.skip, this.pageSize, filter)
      .subscribe(result => {
        // result.value.map((e) => this.taskService.toLowCase(e));
        this.todoList = result.value;
        this.gridView = {
          data: this.todoList,
          total: result['@odata.count']
        };
      });
  }
  // 分页
  pageChange(event: PageChangeEvent): void {
    this.skip = event.skip;
    this.getToDoList();
  }
  /**
   * 列表选择
   * @param event 选中事件
   */
  onSelectionChange(event: SelectionEvent): void {
    this.enable = false;
    if (event.selected) {
      this.selectedWorkitem = event.selectedRows[0].dataItem;
    } else {
      this.selectedWorkitem = null;
    }
  }
  /**
   *  显示弹窗
   */
  onApproveClick(action: number): void {
    if (action === 5) { // 刷新操作
      this.onRefresh();
    } else {
      this.approveDialog.load(action);
    }
  }
  /**
   *  刷新
   */
  onRefresh(): void {
    this.getToDoList();
  }
  /**
   * 打开审批联查界面
   * @param operator 联查、审批操作 1 带审批区域
   */
  navigate(operator: number): void {
    // TODO改用GSPFuncService
    this.frameworkSvc.openFuncByUrl('', '');
    // this.router.navigateByUrl('detail/' + operator);
  }

  onExcute(context: any): void {
    this.taskService.excuteTask(this.selectedWorkitem.Id, context)
      .subscribe(() => {
        this.selectedWorkitem = null;
        this.onRefresh();
      }, (e) => {
        e && e.error && e.error && e.error.Message && alert(e.error.Message);
      });
  }
}



